<template>
  <div class="pd3 border-box">
    <section id="page_main">
        <article>
          <h4>오늘의 사또</h4>
          <div class="pd15 bg-fff-0_6 radius10 txt-c" style="box-shadow:0 1px 1px #bbb;">
              <p class="mg0 font14" v-if="state.nowQuiz.quiz_url">
                참여시간: 08:00 ~ 19:59<br>
                정답공개: 20:45 ~<br>
                <strong class="inline-block mg-t10">*위 시간을 준수해주세요.</strong>
              </p>
              <div class="txt-c mg-b10">
                  <strong class="mg-r3 inline-block radius50p txt-c border-box bg-ddd" style="width:9vw; height:9vw;"
                  v-for="idx in 6" :key="idx">?</strong>
                  <strong class="mg-r3">+</strong>
                  <strong class="mg-r3 inline-block radius50p txt-c border-box bg-ddd" style="width:9vw; height:9vw;">?</strong>
              </div>
              <div class="txt-c" v-if="!state.isShowNumberBoard">
                <button class="w70 pd10 border0 outline0 mg-r5 radius50 font12 border-eee" style="background:#fd0;" @click="showNumberBoard();"><strong>참여</strong></button>
              </div>
              <div class="" v-if="state.isShowNumberBoard">
                <h4 class="txt-c mg0 mg-b5">
                  {{state.pickedBalls.length}} / 6 
                </h4>
                <div class="mg-b10">
                  <div class="mg-b3" v-for="idx in 5" :key="idx">
                    <span class="inline-block radius50p border-box font11 color-fff" style="width:7.9vw; height:7.9vw;" @click="pickTheBall($event)"
                    v-for="idx2 in 9" :key="idx2" :style="{background: state.ballColors[idx - 1]}"
                    :class="{'mg-r3': idx2 != 9}">{{((idx - 1) * 9) + idx2}}</span>
                    <!-- :class="{'mg-r3': idx2 != 9, 'op0_2': state.pickedBalls.indexOf(((idx - 1) * 9) + idx2) !== -1}">{{((idx - 1) * 9) + idx2}}</span> -->
                  </div>
                </div>
                <div class="mg-b10 border-t-eee">
                  clue
                </div>
                <div class="txt-c">
                  <button class="w80 pd10 border0 outline0 mg-r5 radius50 font12 border-eee" style="background:#fd0;"><strong>제출</strong></button>
                  <button class="w70 pd10 border0 outline0 mg-r5 radius50 font12 border-eee bg-fff"><strong>단서</strong></button>
                  <button class="w60 pd10 border0 outline0 mg-r5 radius50 font12 border-eee bg-fff" @click="showNumberBoard();"><strong>취소</strong></button>
                </div>
              </div>
          </div>

        </article>
        <article>
          <h4 class="mg-b10 color-333" @click="getNowQuiz();">현재 퀴즈</h4>
          <!-- <img :src="'http://localhost/viewImg?img=' + state.nowQuiz.quiz_url" class="block w100p mg-b10" />
          <img :src="'http://localhost/viewImg?img=' + state.nowQuiz.hint_url" class="block w100p mg-b10"
          v-if="state.control_now_quiz.hint" /> -->
          <div class="pd15 bg-fff-0_6 radius10" style="box-shadow:0 1px 1px #bbb;">
            <p class="mg0 font14" v-if="!state.nowQuiz.quiz_url">
              아침 08:00 ~ 11:00<br/>
              점심 12:00 ~ 14:00<br/>
              저녁 18:00 ~ 23:00<br/>
              <strong class="inline-block mg-t10">*위 시간에 퀴즈가 올라옵니다.</strong>
            </p>
            <div v-else>
              <img :src="state.nowQuiz.quiz_url" class="block w100p mg-b10" />
              <img :src="state.nowQuiz.hint_url" class="block w100p mg-b10"
              v-if="state.control_now_quiz.hint" />
              <div class="mg-b5">
                <input type="text" class="radius5 border-eee pd10 w100p block border-box font-w900" placeholder="정답을 입력해주세요." v-model="state.newAnswer" />
              </div>
              <div class="txt-c">
                  <button id="btn_submit" class="w70 pd10 border0 outline0 mg-r5 radius50 font12 border-eee" style="background:#fd0;"
                  @click="submitTheAnswer(1, state.nowQuiz.seq_q);">
                      <strong>제출</strong>
                  </button>
                  <button id="btn_hint" class="w60 pd10 border0 outline0 mg-r5 radius50 font12 bg-fff border-eee"
                  v-if="state.nowQuiz.hint_url" @click="showNowHint();">
                      <strong>힌트</strong>
                  </button>
                  <button id="btn_answer" class="w80 pd10 border0 outline0 radius50 font12 bg-fff border-eee">
                      <strong>정답확인</strong>
                  </button>
              </div>
            </div>
          </div>
        </article>
        <article id="box_quized">
          <h4 class="mg-b10 color-333">이전 퀴즈</h4>
            <ul id="quizedList" class="list-style-none mp0">
                <li class="pd15 bg-fff-0_6 radius10 mg-b10" style="box-shadow:0 1px 1px #bbb;" @click="getOldQuiz(q.seq_q);" v-for="q  in state.oldQuizList" :key="q.seq_q">
                    <h5 class="mg0 mg-b10 border-b-dashed-ccc pd-b10">
                      <span>{{q.release_dt}} {{q.release_timing_kor}}</span>
                      <strong v-if="q.total_answer_cnt >= 100">(정답률: {{(q.correct_answer_cnt / q.total_answer_cnt * 100).toFixed(2)}}%)</strong>
                    </h5>
                    <div class="cf">
                        <div class="float-l w30p"
                        :class="{ 'w100p': state.viewOldQuiz === q.seq_q }">
                            <img :src="q.quiz_url" class="block w100p" />
                            <img :src="q.hint_url" class="block w100p mg-t5"
                            v-if="state.viewOldQuizHint == q.seq_q" />
                        </div>
                        <div class="float-r w70p pd-l10 border-box" v-if="state.viewOldQuiz !== q.seq_q">
                            <ul class="mp0 list-style-none font12">
                              <li v-if="q.seq_m1">1등: {{q.seq_m1}}</li>
                              <li v-if="q.seq_m2">2등: {{q.seq_m2}}</li>
                              <li v-if="q.seq_m3">3등: {{q.seq_m3}}</li>
                            </ul>
                        </div>
                    </div>
                    <div class="mg-t5" v-if="state.viewOldQuiz === q.seq_q">
                        <div class="mg-b5">
                          <input type="text" class="radius5 border-eee pd10 w100p block border-box font-w900" placeholder="정답을 입력해주세요." v-model="state.oldAnswer" />
                        </div>
                        <div class="txt-c">
                            <button class="w70 pd10 border0 outline0 mg-r5 radius50 font12 border-eee"
                            style="background:#fd0" @click="submitTheAnswer(0, q.seq_q);">
                                <strong>제출</strong>
                            </button>
                            <button class="w60 pd10 border0 outline0 mg-r5 radius50 font12 bg-fff border-eee" @click="getOldQuizHint(q.seq_q);">
                                <strong>힌트</strong>
                            </button>
                            <button class="w80 pd10 border0 outline0 radius50 font12 bg-fff border-eee">
                                <strong>정답확인</strong>
                            </button>
                        </div>
                    </div>
                </li>
            </ul>
            <div class="txt-c pd-v10" @click="getOldQuizList();">더보기</div>
        </article>
    </section>
  </div>
</template>
<script>
import { reactive, ref } from "@vue/reactivity";
import axios from 'axios';
import { useStore } from "vuex";
import node from '../config/backEnd.js';

export default {
  setup() {
    const store = useStore();
    const state = reactive({
      newAnswer: '',
      oldAnswer: '',
      viewOldQuiz: '',
      viewOldQuizHint: '',
      oldQuizList: [
        // {
        //   quiz_url: '',
        //   hint_url: '',
        //   answer_url: '',
        //   release_timing_kor: ''
        // }
      ],
      nowQuiz: {
        quiz_url: '',
        hint_url: ''
      },
      oldQuizListCnt: 5,

      control_now_quiz: {
        hint: false
      },


      ballColors: ['#F15A5A', '#F0C419', '#4EBA6F', '#2D95BF', '#955BA5'],
      pickedBalls: [],

      isShowNumberBoard: false,
    })

    //ref
    const newAnswer = ref(null);
    //ref


    //method

    const pickTheBall = (target) => {
      let thisTarget = target.target;
      let tmpArr = state.pickedBalls;

      // let elemIdx = tmpArr.indexOf(thisTarget.innerHTML);

      // console.log(elemIdx);

      // if(elemIdx === -1){
      //   tmpArr.push(thisTarget.innerHTML);
      // }else{
      //   tmpArr.splice(elemIdx, 1);
      // }

      // console.log(state.pickedBalls);

      if(!thisTarget.classList.contains('op0_2')){
        if(tmpArr.length > 5){
          alert('6개를 넘을 수 없습니다.');
          return;
        }
        thisTarget.classList.add('op0_2');
        tmpArr.push(thisTarget.innerHTML);
      }else{
        thisTarget.classList.remove('op0_2');

        let elemIdx = tmpArr.indexOf(thisTarget.innerHTML);
        if (elemIdx > -1){
          tmpArr.splice(elemIdx, 1);
        }
      }

      state.pickedBalls = tmpArr;
    }

    const showNumberBoard = () => {
        state.isShowNumberBoard = !state.isShowNumberBoard;
        state.pickedBalls = [];
    }


    const submitTheAnswer = (isNew, seq_q) => {
      let answer = '';
      let isNewQuiz = isNew ? true : false;
      answer = isNew ? state.newAnswer : state.oldAnswer;

      if(confirm('\'' + answer + '\' (으)로 제출하시겠습니까?')){
        axios.post(`${node.nodeUrl}/submitTheAnswer`, {
          seq_q: seq_q,
          seq_m: store.state.userInfo.seq_m,
          nickname: store.state.userInfo.nickname,
          isNew: isNewQuiz,
          answer: answer
        }).then((res) => {
          // console.log(res.data);
          if(res.data.timeOver){
            alert('제출시간이 초과되었습니다.\n새로고침 부탁드립니다.');
            return;
          }

          let tmpData = {};
          tmpData = res.data;
          tmpData.isNew = isNewQuiz;

          store.state.resultPopData = tmpData;
          // console.log(store.state.resultPopData);
          store.commit('popupControl', {isOpen: true, name: 'result'});

          // state.todos = res.data;
        });
        // alert('제출되었습니다.');
        // isNew ? state.newAnswer = '' : state.oldAnswer = '';
      }
    }

    const getOldQuiz = (oldQuizSeq) => {
      state.viewOldQuiz = oldQuizSeq;
      state.oldAnswer = '';
      state.viewOldQuizHint = '';
    }

    const getOldQuizHint = (oldQuizSeq) => {
      if(confirm('광고가 포함됩니다.\n진행하시겠습니까?')){
        setTimeout(function(){
          state.viewOldQuizHint = oldQuizSeq;
        })
      }
    }

    const getNowQuiz = () => {  //현재퀴즈 가져오기
      axios.post(`${node.nodeUrl}/getNowQuiz`).then((res) => {
        if(!res.data.length) return;
        state.nowQuiz = res.data[0];
      });
    }

    const showNowHint = () => { //현재퀴즈 힌트보기
      if(state.control_now_quiz.hint) return;
      if(confirm('광고가 노출됩니다.\n진행하시겠습니까?')){
        state.control_now_quiz.hint = true;
      }
    }

    const getOldQuizList = () => {
      axios.post(`${node.nodeUrl}/getOldQuizList`, {cnt: state.oldQuizListCnt}).then((res) => {
        console.log(res.data);
        state.oldQuizList = res.data;
        state.oldQuizListCnt += 5;

        // console.log(state.oldQuizList);
        // if(!res.data.length) return;

        // state.nowQuiz = res.data[0];
      });
    }

    //method

    //create
    // getNowQuiz();
    // getOldQuizList();
    //create



    return {
      //var
      state,
      //var

      //ref
      newAnswer,
      //ref
      
      //method
      submitTheAnswer,
      getOldQuiz,
      getOldQuizHint,
      getNowQuiz,
      getOldQuizList,
      showNowHint,

      pickTheBall,
      showNumberBoard,
      //method

      store,


    }
  },
}
</script>