import { createStore } from "vuex";
import axios from 'axios';
import node from './config/backEnd.js';

export default createStore({
    state: {
        ref: {},
        isMobile: false,
        pageName: 'main',
        popup: {
            name: 'login',
            isOpen: true,
        },
        userSeq: '',
        userId: '',
        userInfo: {
          point_now: 0,
          correct_answer_cnt: 0,
          incorrect_answer_cnt: 0,
        },
        resultPopData: {

        },
        cookie: { //위치 변경 예정
          get: function (name) {
              var nameEQ = name + "=";
              var ca = document.cookie.split(';');
              for (var i = 0; i < ca.length; i++) {
                  var c = ca[i];
                  while (c.charAt(0) == ' ') c = c.substring(1, c.length);
                  if (c.indexOf(nameEQ) == 0) return c.substring(nameEQ.length, c.length);
              }
              return null;
          },
          set: function (name, value, days) {
              var expires = "";
              if (days) {
                  var date = new Date();
                  date.setTime(date.getTime() + (days * 24 * 60 * 60 * 1000));
                  expires = "; expires=" + date.toUTCString();
              }
              document.cookie = name + "=" + (value || "") + expires + "; path=/";
          }
      }
    },
    mutations: {

        popupControl(state, option) {
            state.popup.isOpen = option.isOpen;
            state.popup.name = option.name;
        },

        getUserInfo(state) {
          axios.post(`${node.nodeUrl}/getUserInfo`, {id: state.userId}).then((res) => {
            if(res.data.length){
              state.userInfo = res.data[0];
              // state.cookie.set('id', res.data[0].id);
              console.log(state.userInfo);
            }else{
              alert('유저 정보를 가져올 수 없습니다.');
            }
          });
        },

        isMobile(state) {
          let UserAgent = navigator.userAgent;
          if (UserAgent.match(/iPhone|iPod|Android|Windows CE|BlackBerry|Symbian|Windows Phone|webOS|Opera Mini|Opera Mobi|POLARIS|IEMobile|lgtelecom|nokia|SonyEricsson/i) != null
          || UserAgent.match(/LG|SAMSUNG|Samsung/) != null){
            state.isMobile = true;
          }else{
            state.isMobile = false;
          }
        }

    },

    ref: {

    },
});