<template>
  <section class="w100p h100vh-20 pd10 border-box">
      <article v-if="state.page == 'init'">
        <!-- <div class="mg-b10">
          <strong>핸드폰번호</strong>
          <div class="cf border-eee mg-t5">
            <input type="number" class="float-l block border0 radius0 pd10 w100p-100 border-box h40" placeholder="01012345678" ref="tel"
            :class="{'op0': !store.state.isMobile}" @keydown.enter="loginFromPC(tel.value);">
            <button class="float-r pd10 border0 outline0 font12 w100 block h40" style="background:#e7c13c;">
                <strong>인증번호 요청</strong>
            </button>
          </div>
        </div> -->

        <!-- <div v-if="store.state.isMobile">
          <strong>인증번호</strong>
          <div class="cf border-eee mg-t5">
            <input type="text" class="float-l block border0 radius0 pd10 w100p-80 border-box h40">
            <button class="float-r pd10 border0 outline0 font12 w80 block h40" style="background:#e7c13c;">
                <strong>인증</strong>
            </button>
          </div>
        </div> -->

        <div class="mg-b10">
          <div class="mg-b10">
            <strong>아이디</strong>
            <input type="text" class="block radius0 pd10 w100p border-box h40 border-eee mg-t5" ref="signIn_id"
            @keydown.enter="loginFromPC(tel.value);">
          </div>
          <div class="mg-b10">
            <strong>비밀번호</strong>
            <input type="password" class="block border0 radius0 pd10 w100p border-box h40 border-eee mg-t5" ref="signIn_pw">
          </div>
          <div class="txt-c">
            <button class="pd10 border0 outline0 font14 w80 radius5 border-eee mg-r5" style="background:#fd0;"
            @click="login(false)">
                <strong>로그인</strong>
            </button>
            <button class="pd10 border0 outline0 font14 w80 radius5 bg-fff border-eee" @click="state.page = 'signUp'">
                <strong>회원가입</strong>
            </button>
          </div>
        </div>
      </article>
      <article v-if="state.page === 'signUp'">
        <div class="mg-b10">
          <strong>아이디 <span class="color-red font8">*아이디는 6~12 영문, 숫자만 사용가능</span></strong>
          <div class="cf border-eee mg-t5">
            <input type="text" class="float-l block border0 radius0 pd10 w100p-100 border-box h40"
            minlength="6" maxlength="12" ref="signUp_id" v-model="state.signUpId" @keyup="inputWithEnAndNum(signUp_id);">
            <button class="float-r pd10 border0 outline0 font12 w100 block h40 border-l-eee bg-fff" @click="chkDuplId();">
                <strong>중복확인</strong>
            </button>
          </div>
        </div>
        <div class="mg-b10">
          <strong>비밀번호 <span class="color-red font8">*비밀번호는 4~20자리</span></strong>
          <input type="password" class="block border0 radius0 pd10 w100p border-box h40 border-eee mg-t5"
          ref="signUp_pw" v-model="state.signUpPw" @change="chkValid(signUp_pw.value, {mnLeng: 4, mxLeng: 20});">
        </div>
        <div class="mg-b10">
          <strong>비밀번호 확인</strong>
          <input type="password" class="block border0 radius0 pd10 w100p border-box h40 border-eee mg-t5"
          ref="signUp_chkPw" v-model="state.chkedPw" @change="chkPw();">
        </div>
        <div class="mg-b10">
          <strong>닉네임 <span class="color-red font8">*닉네임 변경 불가능(2~8자)</span></strong>
          <div class="cf border-eee mg-t5">
            <input type="text" class="float-l block border0 radius0 pd10 w100p-100 border-box h40"
            ref="signUp_nickNm" v-model="state.signUpNickNm">
            <button class="float-r pd10 border0 outline0 font12 w100 block h40 border-l-eee bg-fff" @click="chkDuplNickNm();">
                <strong>중복확인</strong>
            </button>
          </div>
        </div>
        <div class="mg-b10">
          <strong>전화번호 <span class="color-red font8">*허위로 기입한 경우 불이익을 발생</span></strong>
          <input type="tel" class="block border0 radius0 pd10 w100p border-box h40 border-eee mg-t5" ref="signUp_tel" v-model="state.signUp_tel">
        </div>
        <div class="txt-c">
            <button class="pd10 border0 outline0 font14 w80 radius5 border-eee mg-r5" style="background:#fd0;"
            @click="signUp()">
                <strong>회원가입</strong>
            </button>
            <button class="pd10 border0 outline0 font14 w80 radius5 bg-fff border-eee" @click="state.page = 'init'">
                <strong>취소</strong>
            </button>
          </div>
      </article>
      <!-- <article class="pd10 border-eee mg-b10" v-if="store.state.isMobile">
        <div>
          <strong>닉네임 <span class="color-red font8">*한번 정한 닉네임은 변경할 수 없습니다.</span></strong>
          <div class="cf border-ccc mg-t5">
            <input type="number" class="float-l block border0 radius0 pd10 w100p-80 border-box h40">
            <button class="float-r pd10 border0 outline0 font12 w80 block h40" style="background:#e7c13c;">
                <strong>중복확인</strong>
            </button>
          </div>
        </div>
      </article> -->
      <!-- <div class="txt-c" v-if="store.state.isMobile">
        <button class="pd10 border0 outline0 font14 w80" style="background:#e7c13c;">
            <strong>가입</strong>
        </button>
        <button class="pd10 border0 outline0 font14 w80" style="background:#e7c13c;"
        @click="login(tel.value)">
            <strong>로그인</strong>
        </button>
      </div> -->
  </section>
</template>
<script>
import { onMounted } from "@vue/runtime-core";
import { reactive, ref } from "@vue/reactivity";
import { useStore } from "vuex";
import axios from 'axios';
import node from '../config/backEnd.js';

export default {
  components: {
  },
  setup() {
    const store = useStore();
    const state = reactive({
      page:  'init',
      signUpId: '',
      chkedId: '',
      signUpPw: '',
      chkedPw: '',
      signUpNickNm: '',
      chkedNickNm: '',
      signUp_tel: '',
    })


    //ref
    const signIn_id = ref(null);
    const signIn_pw = ref(null);
    const signUp_id = ref(null);
    const signUp_pw = ref(null);
    const signUp_chkPw = ref(null);
    const signUp_nickNm = ref(null);
    const signUp_tel = ref(null);
    //ref

    const login = (isCookie) => {
      let tmpObj = {isCookie : isCookie};

      if(isCookie === true){
        tmpObj.id = store.state.cookie.get('id');
      }else{
        tmpObj.id = signIn_id.value.value;
        tmpObj.pw = signIn_pw.value.value;
      }


      axios.post(`${node.nodeUrl}/login`, tmpObj).then((res) => {
        if(res.data.length){
          store.state.userId = res.data[0].id;
          store.state.cookie.set('id', store.state.userId);

          store.commit('getUserInfo', store.state.userId);
          store.commit('popupControl', {isOpen: false, name: ''});
        }else{
          alert('잘못된 접근입니다.');
        }
        // if(res.data.length){
        //   store.state.userSeq = res.data[0].seq_m;
        //   store.commit('getUserInfo', store.state.userSeq);
        //   store.commit('popupControl', {isOpen: false, name: ''});
        // }
      });
    }

    // const login = (tel, isCookie) => {
    //   let telWithoutHyphen = tel.replaceAll("-", "");
    //   if(isCookie) telWithoutHyphen = store.state.cookie.get('tel');
    //   axios.post(`${node.nodeUrl}/login`, {tel: telWithoutHyphen, isMobile: store.state.isMobile}).then((res) => {
    //     if(res.data.length){
    //       store.state.userSeq = res.data[0].seq_m;
    //       store.commit('getUserInfo', store.state.userSeq);
    //       store.commit('popupControl', {isOpen: false, name: ''});
    //     }
    //   });
    // }

    const loginFromPC = (tel) => {
      if(store.state.isMobile) return;
      login(tel);
    }

    const chkDuplId = () => {
      if(!chkValid(state.signUpId, {mnLeng: 6, mxLeng: 12})) return;
      axios.post(`${node.nodeUrl}/chkDuplId`, {id: state.signUpId}).then((res) => {
        if(res.data.length){
          alert('다른 ID를 입력해주세요.');
          state.chkedId = '';
        }else{
          alert('사용가능한 ID입니다.');
          state.chkedId = state.signUpId;
        }
      });
    }

    const chkDuplNickNm = () => {
      if(!chkValid(state.signUpId, {mnLeng: 4, mxLeng: 16})) return;
      axios.post(`${node.nodeUrl}/chkDuplNickNm`, {nickNm: state.signUpNickNm}).then((res) => {
        if(res.data.length){
          alert('다른 닉네임을 사용해 주세요.');
          state.chkedNickNm = '';
        }else{
          alert('사용가능한 닉네임입니다.');
          state.chkedNickNm = state.signUpNickNm;
        }
      });
    }

    const chkValid = (value, option) => {
      let isValid = true;
      if(option.mnLeng > value.length || value.length > option.mxLeng) {
        // alert('자리수를 확인해 주세요.\n(' + option.mnLeng + '~' + option.mxLeng + '자리)');
        if(option.alert !== false) alert('자리수를 확인해 주세요.');
        isValid = false;
      }
      return isValid;
    }

    const inputWithEnAndNum = (target) => { //영어랑 숫자만
      target.value = target.value.replace(/[^a-z0-9]/gi,'');
    }

    const chkPw = () => {
      if(state.signUpPw !== state.chkedPw){
        alert('비밀번호가 일치하지 않습니다.');
      }
    }

    const signUp = () => {
      //유효성검사
      if(!chkValid(state.signUpId, {mnLeng: 6, mxLeng: 12, alert: false})){
        alert('아이디는 6~12자 입니다.');
        return;
      }

      if(state.signUpId !== state.chkedId){
        alert('아이디 중복체크를 해주세요.');
        return;
      }

      if(!chkValid(state.signUpPw, {mnLeng: 4, mxLeng: 20, alert: false})){
        alert('비밀번호는 4~20자 입니다.');
        return;
      }

      if(state.signUpPw !== state.chkedPw){
        alert('비밀번호가 일치하지 않습니다.');
        return;
      }

      if(!chkValid(state.signUpNickNm, {mnLeng: 4, mxLeng: 16, alert: false})){
        alert('닉네임은 2~8자 입니다.');
        return;
      }

      if(state.signUpNickNm !== state.chkedNickNm){
        alert('닉네임 중복체크를 해주세요.');
        return;
      }

      axios.post(`${node.nodeUrl}/signUp`, {
        id: state.chkedId,
        pw: state.chkedPw,
        nickNm: state.chkedNickNm,
        tel: state.signUp_tel.replaceAll("-", ""),
        }).then((res) => {

        store.state.cookie.set('id', res.data[0].id);
        // console.log(res.data);
        login(true);
      });
    }

    // create
    login(true);
    // create

    onMounted(() => {
      //네이티브로 전화번호 가져오기
      //번호가 없으면 번호를 가져올 수 없다고 수동입력 시키기
    });

    return {
      //method
      login,
      loginFromPC,
      chkDuplId,
      chkDuplNickNm,
      signUp,
      chkValid,
      inputWithEnAndNum,
      chkPw,

      //method

      //var
      state,
      //var


      //ref
      signIn_id,
      signIn_pw,
      signUp_id,
      signUp_pw,
      signUp_chkPw,
      signUp_nickNm,
      signUp_tel,
      //ref

      store,
    };
  },
};
</script>